.tooltipWrapper {
  position: relative;
  display: inline-block;
}

.targetElement {
  cursor: pointer;
}

.tooltip {
  position: absolute;
  width: 100px;
  font-size: 14px;
  box-sizing: border-box;
  background-color: #555;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px;
  z-index: 9999;
}

.tooltip::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #555 transparent transparent transparent;
}

.tooltip.top {
  bottom: 113%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  left: 50%;
}

.tooltip.bottom {
  top: 130%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  left: 50%;
}

.tooltip.right {
  left: 130%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  top: 50%;
}

.tooltip.left {
  right: 130%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  top: 50%;
}

